const formatNum = num => {
  return num < 10 ? "0" + num : num + "";
};

export default {
  name: "SimpleDatetimePicker",
  props: {
    startYear: {
      type: Number,
      default: 2000
    },
    endYear: {
      type: Number,
      default: 2030
    },
    color: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      open: false,
      years: [],
      months: [],
      days: [],
      hours: [],
      minutes: [],

      currentDate: new Date(),
      year: "",
      month: "",
      day: "",
      hour: "",
      minute: "",

      value: [0, 0, 0, 0, 0]
    };
  },

  mounted() {
    this.init();
  },

  watch: {
    month() {
      this.initDays();
    }
  },

  filters: {
    formatNum(num) {
      return formatNum(num);
    }
  },

  methods: {
    init() {
      this.initYears();
      this.initMonths();
      this.initDays();
      this.initHours();
      this.initMinutes();
      this.setSelectValue();
    },

    initYears() {
      let years = [];
      for (let i = this.startYear; i <= this.endYear; i++) {
        years.push(i);
        if (this.currentDate.getFullYear() === i) {
          this.$set(this.value, 0, i - this.startYear);
        }
      }
      this.years = years;
    },

    initMonths() {
      let months = [];
      for (let i = 1; i <= 12; i++) {
        months.push(i);
        if (this.currentDate.getMonth() + 1 === i) {
          this.$set(this.value, 1, i - 1);
        }
      }
      this.months = months;
    },

    initDays() {
      let selectedYear = this.years[this.value[0]];
      let selectedMonth = this.months[this.value[1]];

      let days = [];
      let totalDays = new Date(selectedYear, selectedMonth, 0).getDate();

      for (let i = 1; i <= totalDays; i++) {
        days.push(i);
        if (this.currentDate.getDate() === i) {
          this.$set(this.value, 2, i - 1);
        }
      }
      this.days = days;
    },

    initHours() {
      let hours = [];
      for (let i = 0; i <= 23; i++) {
        hours.push(i);
        if (this.currentDate.getHours() === i) {
          this.$set(this.value, 3, i);
        }
      }
      this.hours = hours;
    },

    initMinutes() {
      let minutes = [];
      for (let i = 0; i < 60; i++) {
        minutes.push(i);
        if (this.currentDate.getMinutes() === i) {
          this.$set(this.value, 4, i);
        }
      }
      this.minutes = minutes;
    },

    show() {
      this.open = true;
    },

    hide() {
      this.open = false;
    },

    _onChange(e) {
      const value = e.detail.value;
      this.value = e.detail.value;
      this.setSelectValue();
    },

    setSelectValue() {
      this.year = this.years[this.value[0]];
      this.month = this.months[this.value[1]];
      this.day = this.days[this.value[2]];
      this.hour = this.hours[this.value[3]];
      this.minute = this.minutes[this.value[4]];
    },

    _onSubmit() {
      const result = {
        year: formatNum(this.year),
        month: formatNum(this.month),
        day: formatNum(this.day),
        hour: formatNum(this.hour),
        minute: formatNum(this.minute)
      };
      this.$emit("submit", result);
      this.hide();
    }
  }
};
